h1 {
  color: #111;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 64px;
  font-weight: 700;
  line-height: 64px;
  margin: 0 0 0;
  padding: 20px 30px;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  color: #111;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  margin: 0 0 24px;
  padding: 0 30px;
  text-align: center;
  text-transform: uppercase;
}

p {
  color: #111;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 28px;
  margin: 0 0 48px;
}

a {
  color: #990000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.date {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.date:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.author {
  color: #111;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding-bottom: 38px;
  position: relative;
  text-align: center;
  z-index: 1;
}

.author:before {
  border-top: 1px solid #111;
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: -1;
}

.date span,
.author span {
  background: #fdfdfd;
  padding: 0 10px;
  text-transform: uppercase;
}

.line {
  border-top: 1px solid #111;
  display: block;
  margin-top: 60px;
  padding-top: 50px;
  position: relative;
}

.read-more {
  -moz-border-radius: 50%;
  -moz-transition: all 0.2s ease-in-out;
  -webkit-border-radius: 50%;
  -webkit-transition: all 0.2s ease-in-out;
  background: #111;
  border-radius: 50%;
  border: 10px solid #fdfdfd;
  color: #fff;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 80px;
  line-height: 80px;
  margin: -40px 0 0 -40px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  text-align: center;
  text-transform: uppercase;
  width: 80px;
}

.read-more:hover {
  background: #990000;
  text-decoration: none;
}
