.container {
  height: 100%;
  display: grid;
  grid-template-columns: 30% repeat(4, 10%) 30%;
  grid-template-rows: 12.5% 5% 20% repeat(5, 10%) 12.5%;
  height: 95vh;
  width: 95vw;
  background-color: #696969;
}


@media only screen and (max-width: 767px) {
  .container {
    grid-template-columns: 6% repeat(4, 22%) 6%;
    grid-template-rows: 5% 5% 20% repeat(5, 13%) 5%;
  }
}

/* Grid Classes - Columns */
.coll-1 {
  grid-column: 2 / 3;
}
.coll-2 {
  grid-column: 3 / 4;
}
.coll-3 {
  grid-column: 4 / 5;
}
.coll-4 {
  grid-column: 5 / 6;
}
.coll-1-2 {
  grid-column: 2 / 4;
}
.coll-1-3 {
  grid-column: 2/5;
}
.coll-1-4 {
  grid-column: 2 / 6;
}
/* Grid Classes - Rows */
.row-1 {
  grid-row: 2 / 3;
}
.row-2 {
  grid-row: 3 / 4;
}
.row-3 {
  grid-row: 4 / 5;
}
.row-4 {
  grid-row: 5 / 6;
}
.row-5 {
  grid-row: 6 / 7;
}
.row-6 {
  grid-row: 7 / 8;
}
.row-7 {
  grid-row: 8 / 9;
}
.row-1-2 {
  grid-row: 2 / 4;
}

