.Clock {
  background-color: #282c34;
  min-height: 130vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
  overflow: hidden;
}

.box {
  position: relative;
  width: 1px;
  height: 1px;
  transition: all 0.5s;
}

.item {
  position: absolute;
  right: 0;
  top: -1rem;
  text-align: right;
  line-height: 2rem;
  opacity: 0.5;
  transition: all 0.5s;
}

.item.active {
  opacity: 1;
  font-size: 1.4rem;
}

.month {
  width: 200px;
  right: -100px;
}

.day {
  width: 480px;
  right: -240px;
}

.week {
  width: 360px;
  right: -180px;
}

.hour {
  width: 600px;
  right: -300px;
}

.Minute {
  width: 800px;
  right: -400px;
}

.second {
  width: 960px;
  right: -480px;
}

.msg{
    position: absolute;
    left: 2rem;
    top: 11rem;
    text-align: left;
    line-height: 2rem;
    width: 300px;
  }

  .msg div{
    opacity: .8;
  }

  .msg .year{
    margin-bottom:1rem;
    opacity: 1;
  }
  
  .msg .year span{
    font-size: 2rem;
  }
  
  .msg_month{
    position: absolute;
    right: 2rem;
    bottom: 2rem;
    font-size: 2rem;
  }
