button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  margin: 0;
  text-align: center;
  text-decoration: none;
  border: 1px solid black;
  cursor: grab;
  font-size: 2.5em;
}

button:hover {
  opacity: 0.7;
  border: 1.5px solid white;
}

button:focus {
  outline: 0;
}

.num {
  background-color: #a9a9a9;
}
.oper {
  background-color: orange;
}
